@import "variables";

#step4 {
  background: #171753;
  color: $white;
  padding: 75px 0;
  min-height: 456px;
  .container {
    .earn-koi-description {
      h3 {
        margin-bottom: 42px;
      }
      p {
        margin-bottom: 41px;
      }
    }
  }
  .earn-koi-img-wrapper {
    padding-left: 80px;
    display: flex;
    img {
      flex: 1;
    }
  }
  @media (max-width: $sm) {
    padding: 56px 0;
    .container {
      .earn-koi-description {
        h3 {
          line-height: 24px;
        }
        p {
          line-height: 28px;
        }
      }
    }
    .earn-koi-img-wrapper {
      padding-left: 15px;
    }
  }
}
